<template>
  <div class="common-detail">
    <div class="content">
      <Form :label-width="120">
        <FormItem label="标题：">
          <label>{{resData.title}}</label>
        </FormItem>
        <FormItem label="是否公开：">
          <label>{{resData.ifShow=='true'?'是':'否'}}</label>
        </FormItem>
        <FormItem label="发布机构：">
          <label>{{resData.pubOrg}}</label>
        </FormItem>
        <FormItem label="发布时间：">
          <label>{{resData.createTime}}</label>
        </FormItem>
        <FormItem label="编辑内容：">
          <label>{{resData.content}}</label>
        </FormItem>
        <FormItem label="附件：">
                <showUsualFiles :file-obj="fileObj" :key="keyId"></showUsualFiles>
        </FormItem>
      </Form>
    </div>
  </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";

export default {
  props: ["dataObj"],
  components: {
    showUsualFiles
  },
  data() {
    return {
      keyId: "",
      resData: {},
      fileObj:{},
    };
  },
  created() {
    if (Object.keys(this.dataObj.data).length > 0) {
      let datas = this.dataObj.data;
      if (datas && datas.id) {
        // 调用浏览次数接口
        if(this.dataObj && this.dataObj.infViewNum){
          this.getInfInfoRowNum(datas.id);
        }else{
          this.getInfInfoRow(datas.id);
        }
      }
    }
  },
  methods: {
    getInfInfoRow(id) {
      this.axios
        .post("/process/infInfo/getInfInfoRow/" + id +'?decode=1', {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.resData = response.data;
            this.resData.createTime = this.resData.createTime.substr(0,19)
            this.keyId = response.data.busId;
            this.fileObj = {
                data:response.data,
                showFlag:false,
            }
          }
        })
        .catch(error => {});
    },
    getInfInfoRowNum(id) {
      this.axios
        .post("/process/infInfo/getInfInfoRow/" + id+'?infViewNum=1', {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.resData = response.data;
            this.resData.createTime = this.resData.createTime.substr(0,19)
            this.keyId = response.data.busId;
            this.fileObj = {
                data:response.data,
                showFlag:false,
            }
          }
        })
        .catch(error => {});
    }
  }
};
</script>


