<template>
  <div class="show-common-manage">
    <div class="topTitle">
      <span class="topTitle-span1">反洗钱信息</span>
      <span class="topTitle-span1">
        <img src="../../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">信息库</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
        </div>
        <div class="contentCellBody1">
          <Form ref="formValidate" :model="formValidate" :label-width="120" inline>
            <Row>
              <FormItem label="标题：">
                <Input v-model="formValidate.title" placeholder="请输入标题" style="width:200px"></Input>
              </FormItem>
              <FormItem label="发布时间：">
                <DatePicker
                  type="daterange"
                  placeholder="请选择发布时间"
                  :value="formValidate.permissionTime"
                  @on-change="formValidate.permissionTime=$event"
                  style="width:200px"
                ></DatePicker>
              </FormItem>
              <FormItem label="编辑内容：">
                <Input v-model="formValidate.content" placeholder="请输入内容" style="width:200px"></Input>
              </FormItem>
            </Row>
            <Row>
              <div class="btnClass">
                <Button type="primary" @click="handleSubmit(0, 1)">查询</Button>
              </div>
            </Row>
          </Form>
        </div>
      </div>
    </div>
    <div style="padding-left:20px">
      <div class="contentCellTitle">
        <div class="contentCellTitle-1"></div>文件展示
      </div>
    </div>
    <div class="file-content" v-for="cardData in cardDatas" :key="cardData.id">
      <div class="file-detail">
        <div class="left-detail">
          <div class="header">
            <img src="../../../../assets/tip.png" alt />
            <span class="info">信息库</span>
          </div>
          <div class="header-content">
            <div class="title mr50">
            <el-popover
                placement="top-start"
                title
                width="200"
                trigger="hover"
                :content="cardData.title"
              >
                  <span slot="reference" >{{cardData.title}}</span>
              </el-popover>
            </div>
            <span class="date"><span class="fontWeight">制定单位：</span>{{cardData.manager}}</span>
            <span class="date"><span class="fontWeight">发布时间：</span>{{cardData.updateTime}}</span>
            <span class="date"><span class="fontWeight">发布人：</span>{{cardData.pubUser}}</span>
            <span class="date">
              <span class="fontWeight">浏览次数：</span>
              <label class="spanBox" @click="showViewCount(cardData)">{{cardData.infViewNum}}</label>
            </span>
          </div>
          <div class="content-detail">
            <span class="fontWeight">内容：</span>
            <el-popover
                placement="top-start"
                title
                width="200"
                trigger="hover"
                :content="cardData.content"
              >
                  <span slot="reference" >{{cardData.content}}</span>
              </el-popover>
            </div>
        </div>
        <div class="right-detail">
          <div :class="cardData.ifShow=='true'?'status':'status2'">
            <span>{{cardData.ifShow=='true'?'公开':'未公开'}}</span>
          </div>
          <Button @click="showDetail(cardData)">查看详情</Button>
          <Button @click="watchFiles(cardData)">查看文件</Button>
        </div>
      </div>
    </div>
    <div class="flexCenter pageBox">
      <Page :total="rowCount" 
      :page-size="pageSize" @on-change="pageChange" @on-page-size-change='pageSizefun'
      :current.sync="currentpage"
      show-elevator show-sizer></Page>
    </div>
    <Modal :mask-closable="false"  
      v-model="showModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: '60%'}"
    >
      <component
        @cancel="cancelDialog"
        :is="tabview"
        :dataObj="dataObj"
        :key="keyId"
        :file-obj="dataObj"
      ></component>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";
import viewCountDialog from "./viewCountDialog"; //查看浏览次数
import infoManageDetail from "@/components/processManage/processManage/common/infoManage/infoManageDetail"; //信息库
export default {
  components: {
    viewCountDialog,
    infoManageDetail,
    showUsualFiles
  },
  data() {
    return {
      keyId:"",
      pubOrgDatas: [],//发布机构
      tabview: "viewCountDialog",
      showModal: false,
      dataObj: {
        type: "1", //1新增，2修改
        data: []
      },
      pageSize:10,
      pageNum:1,
      currentpage:1,
      rowCount:0,
      modelTitle: "",
      pageSize: 5,
      formValidate: {
        content: "", //内容
        title: "", //标题
        permissionTime:["",""],
      },
      cardDatas: []
    };
  },

  created() {
    this.getDic();
    this.handleSubmit(0, 1);
  },
  methods: {
    watchFiles(item){
      this.modelTitle = '文件列表';
      this.tabview = "showUsualFiles";
      this.showModal = true;
      this.keyId = item.busId;
      this.dataObj = {
        data:item,
        showFlag:false
      }
    },
    /**
     * name:获取下拉字典
     */
    getDic(){
      this.axios
      .get("/system/getDic/PROCESS_PUB_ORG")
      .then(response => {
        if (response.code == "200") {
          this.pubOrgDatas = response.data.PROCESS_PUB_ORG;
        }
      })
      .catch(function(error) {});
    },
    /**
     * name:查询方法
     * ifExacte:	 0 普通 1 精确
     * pageNo: 页数(1)
     * * */
    handleSubmit(ifExacte, pageNo){
      let data = {
        ifExacte:ifExacte,
        minPubTime:this.formValidate.permissionTime[0],
        maxPubTime:this.formValidate.permissionTime[1],
        content:this.formValidate.content,
        pageNum:pageNo,
        title:this.formValidate.title.trim(),
        pageSize:this.pageSize
      }
      this.axios
      .post("/process/infInfo/getInfInfoList?infViewNum=1", data, {
          headers: { "Content-Type": "application/json" }
        })
      .then(response => {
        if (response.code == "200") {
          this.cardDatas = response.data.records;
          this.cardDatas.forEach(element => {
            element.createTime = element.createTime.substr(0,10);
            element.updateTime = element.updateTime.substr(0,10);
          });
          this.rowCount = response.data.total;
        }
      })
      .catch(error => {});
    },
    pageSizefun(pageSize){
      this.pageSize = pageSize * 1;
      this.handleSubmit(0, 1);
    },
    pageChange(pageNo){
      this.pageNum = pageNo;
      this.handleSubmit(0, pageNo)
    },
    showViewCount(data) {
      this.dataObj = {
        data
      }
      this.keyId = !this.keyId;
      this.modelTitle = "浏览次数";
      this.showModal = true;
      this.tabview = "viewCountDialog";
    },
    cancel() {
      if(this.modelTitle == '详情'){
        this.handleSubmit(0, this.pageNum);
      }
    },
    cancelDialog(val) {
      this.showModal = val;
    },
    showDetail(item) {
      this.modelTitle = "详情";
      this.dataObj = {
        data: item,
        showFlag:false,
        infViewNum:1
      };
      this.tabview = "infoManageDetail";
      this.keyId = !this.keyId;
      this.showModal = true;

    }
  }
};
</script>
<style lang="scss" scoped>
.show-common-manage {
  position: relative;
  background: RGB(238, 243, 248);
  margin-bottom: 20px;
}
</style>


